<template>
  <div class="product-detail">
    <div class="left">
      <img :src="data.img">
    </div>
    <div class="right">
      <div class="title"><span class="icon"></span><span>{{data.title}}</span></div>
      <div class="price">
        <span>价格</span>
        <span class="pro-price">￥{{data.price}}</span>
      </div>
      <div class="coupon">
        <span>优惠券：{{data.coupon}}</span>
        <a v-if="data.coupon !== '无'" :href="data.couponUrl" target="_blank">领券购买</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      data: {
        id: '521879099139',
        img: 'http://img.alicdn.com/bao/uploaded/i2/911810517/TB1QJdRaR_85uJjSZPfXXcp0FXa_!!0-item_pic.jpg',
        productUrl: 'https://s.click.taobao.com/CqaUSFw',
        title: '日系少女装英伦学院风针织校服jk制服套头韩版秋冬班服学生装毛衣',
        ms: 10, // 月售
        price: 10,
        coupon: '满59元减3元',
        couponUrl: 'https://s.click.taobao.com/pZbUSFw',
        couponTb: '￥ysTabF7edrF￥'
      }
    }
  },
  created () {
    this.getRouterData()
  },
  methods: {
    getRouterData () {
      this.data = this.$route.params.data
      console.log(this.data)
    }
  }
}
</script>

<style lang="scss" scoped>
.product-detail {
  height: 500px;
  width: 70%;
  .left {
    float: left;
    width: 50%;
    height: 100%;
    img {
      float: right;
      height: 100%;
    }
  }
  .right {
    width: 50%;
    float: left;
    .title {
      height: 50px;
      line-height: 50px;
      padding: 10px;
      span {
        float: left;
      }
      .icon {
        padding: 15px 0px;
        background-image: url(../../assets/taobao.png);
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        width: 20px;
        height: 20px;
      }
    }
    .price {
      height: 80px;
      line-height: 80px;
      color: #fc4d52;
      font-size: 37px;
      background: #EEEEEE;
      margin: 0px 10px 10px;
      padding: 0 10px;
      >:first-child {
        font-size: 14px;
        color: black;
      }
    }
    .coupon {
      margin: 10px;
      padding: 10px;
      float: left;
      color: #fc4d52;
      font-size: 18px;
      border-radius: 2px;
      font-weight: 400;
      margin: 9px 0;
      position: relative;
      min-width: 57px;
      text-align: center;
      box-sizing: border-box;
      line-height: 14px;
      span {
        border: 1px dashed;
        padding: 5px 10px;
      }
      a {
        background-color: #fc4d52;
        height: 100%;
        padding: 6px 25px;
        color: #ffffff;
        text-decoration: none;
      }
      a:hover {
        box-shadow: 0px 0px 5px #F44336;
      }
    }
  }
}
</style>



